@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Caveat:wght@600&family=Praise&family=Source+Sans+Pro:wght@300&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Caveat:wght@600&family=Open+Sans&family=Praise&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  letter-spacing: .6px;
}
.link-color{
  color: #fff;
  transition: transform .5s;
  padding: 1vh 1vh;
  font-size: 1.3rem;
}
.link-color:hover{
  color: rgb(197, 192, 192);
  transform: scale(1.1);
}
body {
  background-color:rgb(209, 222, 235);
  overflow-y: hidden;
  font-size: 2rem;
}
.mynavbar {
  background-image: url('../image1.jpg');
  text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.719);
}
header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 4vh;
  text-shadow: 4px 4px 10px rgba(0, 0, 0, 0.562);

}
section {
  margin-top: 3vh;
  max-width: 100vw;
  display: flex;
  text-transform: uppercase;
}
.card {
  height: 26vh;
  width: 30vw;
  color: rgb(255, 253, 253);
    background-image: linear-gradient(
    to right,
    rgba(196, 124, 17, 0.842),
    rgba(202, 57, 77, 0.836)
  ); 
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 15px;
  font-size: 2rem;
  margin: 1% 5%;
  box-shadow: 7px 7px 16px rgb(0, 0, 0);
  cursor: pointer;
  transition: transform .7s;
}
.card:hover {
  background-image: linear-gradient(
    to right,
    rgba(156, 97, 9, 0.966),
    rgba(168, 34, 52, 0.932)
  ); 
  transform: scale(1.2);
}
.big {
  font-family: Bebas Neue, sans-serif;
  text-shadow: 3px 3px 28px rgba(0, 0, 0, 0.719);
}
.country-name {
  /* font-size: 8.4vh; */
  font-family: Bebas Neue, sans-serif;
  text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.719);
}
form {
  margin: 2.8vh 22vh;
  padding: 7vh 30vh;
  background-image: url('../image1.jpg');
  background-size: cover;
  background-color: rgb(95, 68, 33);
  height:80vh;
  color: #fff;
  border-radius: 20px;
  box-shadow: 7px 7px 16px rgb(0, 0, 0);
  font-size: 1.3rem;
  letter-spacing: 3px;
}
@media (max-width:1000px) {
 .card{
   font-size: 1.8rem;
 }
}
@media (max-width:800px) {
  .card{
    font-size: 1.4rem;
  }
 }
 @media (max-width:640px) {
  .card{
    font-size: 1.2rem;
  }
 }
 @media (max-width:555px) {
  .card{
    font-size: 1rem;
  }
 }
 @media (max-width:465px) {
  .card{
    font-size: .8rem;
  }
 }